import DeepData from "../../../assets/js/formate.js";
export default {
  data() {
    return {
      uid: "",
      ids: "",
      type_id: "",
      Title: "",
      tableData: [],
      value: "",
      currentPage: 1,
      limit: 10,
      total: 0,
      visible: false,
      disabled: false,
      disabledID: false,
      rules: {
        send_name: [
          { required: true, message: "必填项不能为空", trigger: "blur" }
        ],
        title: [{ required: true, message: "必填项不能为空", trigger: "blur" }],
        content: [
          { required: true, message: "必填项不能为空", trigger: "blur" }
        ],
        coins: [{ required: true, message: "必填项不能为空", trigger: "blur" }],
        uid: [{ required: true, message: "必填项不能为空", trigger: "blur" }]
      },
      form: {
        op_user: "",
        title: "",
        content: "",
        coins: "",
        range: []
      },
      title: "新增"
    };
  },
  created() {
    this.getData()
  },
  methods: {
    //写邮件
    writeEmail() {
      this.editForm("新增", true, false, false, {});
    },

    //按条件搜索
    search() {
      if (this.ids === "") {
        this.ids = 0;
      } else if (this.type_id === "") {
        this.type_id = 0;
      } else if (this.uid === "") {
        this.uid = 0;
      }
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        mail_type: 2,
        title: this.Title,
        uid: +this.uid,
        ids: +this.ids,
        type_id: +this.type_id
      });
      this.ids = "";
      this.type_id = "";
      this.uid = "";
      this.Title = "";
    },

    //表格编辑
    handleEdit(row) {
      this.editForm("编辑", true, false, true, DeepData(row));
    },

    //表格详情
    detail(row) {
      // console.log(row);
      this.editForm("邮件详情", true, true, true, DeepData(row));
    },

    //表格发送邮件
    async send(row) {
      let { data } = await this.$http.HallFunConfig.patchEmail({
        mail_id: row.id
      });
      if (data.code === 1 && data.msg === "ok") {
        this.getData()
        this.$message({
          type: "success",
          message: "邮件已发送!"
        });
      } else {
        this.$message({
          type: "success",
          message: "邮件发送失败!"
        });
      }
    },

    //页容量改变
    handleSizeChange(num) {
      this.limit = num;
      this.currentPage = 1;
      this.getData()
    },

    //页码改变
    handleCurrentChange(pagenum) {
      this.currentPage = pagenum;
      this.getData()
    },

    //表单提交
    onSubmit(formName, type) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          if (type === "新增") {
            let resData = DeepData(this.form);
            resData.mail_type = 2;
            let { data } = await this.$http.HallFunConfig.PostEmail(resData);
            if (data.code === 1 && data.msg === "ok") {
              this.getData()
              this.$message({
                type: "success",
                message: "新增成功!"
              });
            } else {
              this.$message({
                type: "warning",
                message: "新增失败!"
              });
            }
          } else if (type === "编辑") {
            let res = {
              op_user: this.form.op_user,
              title: this.form.title,
              content: this.form.content,
              coins: this.form.coins,
              range: this.form.range,
              mail_type: 2,
              mail_id: this.form.id
            };
            let { data } = await this.$http.HallFunConfig.PutEmail(res);
            if (data.code === 1 && data.msg === "ok") {
              this.getData()
              this.$message({
                type: "success",
                message: "修改成功!"
              });
            } else {
              this.$message({
                type: "warning",
                message: "修改失败!"
              });
            }
          } else if (type === "邮件详情") {
          }
          this.editForm("新增", false, false, false, {});
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    editForm(title, visible, disabled, disabledID, form) {
      this.title = title;
      this.visible = visible;
      this.disabled = disabled;
      this.disabledID = disabledID;
      this.form = form;
    },

    formateData(res) {
      res.forEach(item => {
        if (item.status === 0) {
          item.read_state = "待发送";
        } else if (item.status === 1) {
          item.read_state = item.read_state === 0 ? "未读" : "已读";
        } else if (item.status === 2) {
          item.read_state = "删除";
        }
      });
      return res;
    },

    getData() {
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        mail_type: 2
      });
    },

    async initdata(params) {
      let { data } = await this.$http.HallFunConfig.GetEmail(params);
      let fres = this.formateData(data.data);
      this.tableData = fres;
      this.total = data.total;
    }
  }
};



